body {
  height: 100vw;
  width: 100%;
  margin: 0px;
  padding: 0px;
  font-family: 'Megrim', cursive;
  color: whitesmoke;
  background-image: linear-gradient(to bottom, black, #505050);
}

.app h1 {
  margin: 0px;
  color: #02c002;
  background-image: linear-gradient(to right, black, #26a026);
  padding-top: 25px;
  padding-left: 18px;
}

.header-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-image: linear-gradient(to right, black, #26a026);
  border-bottom: 1px solid limegreen;
}

.header-wrapper .links-wrapper {
  display: flex;
  align-items: center;
}

.header-wrapper .links-wrapper .nav-links {
  width: 90px;
  text-align: center;
}

.header-wrapper .links-wrapper .nav-links a {
  text-decoration: none;
  font-size: 20px;
  color: whitesmoke;
  text-decoration: none;
  transition: .5s;
}

.header-wrapper .links-wrapper a:hover {
  color: limegreen;
  letter-spacing: 2px;
}

.header-wrapper .search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 400px;
  padding-bottom: 10px;
}

.header-wrapper .search-bar input {
  width: 250px;
  height: 25px;
  font-size: 20px;
  font-weight: 600;
  font-family: 'Megrim', cursive;
  border: solid black;
  border-radius: 3px;
  background-color: black;
  color: limegreen;
  outline: none;
}

.header-wrapper .search-bar .icon {
  padding-left: 10px;
  color: black;
  font-size: 1.75em;
}

@media (max-width: 600px) {
  .homepage-wrapper {
    flex-direction: row;
    height: 100%;
  }
}

.slide-container {
  width: 70%;
  margin: auto;
}

h3 {
  text-align: center;
}

.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 300px;
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}

.each-fade {
  display: flex;
}

.each-fade .image-container {
  width: 75%;
  overflow: hidden;
}

.each-fade .image-container img {
  width: 100%;
}

.each-fade h2 {
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background: #adceed;
}
